import axios from 'axios';
class Freshsales {
  createLead = async (formData: any): Promise<void> => {
    // replace with (req: any, res: any)
    try {
      const data = {
        lead: {
          first_name: formData.fname,
          last_name: formData.fname,
          company: { name: formData.company },
          work_number: formData.phone,
          email: formData.email,
        },
      };
      const config = {
        baseURL: 'https://disruptv.freshsales.io/',
        headers: {
          Authorization: `Token token=${process.env.REACT_APP_FRESHSALES}`,
        },
      };

      const response = await axios.post('/api/leads', data, config);
      console.log(response);

      return; // replace with res.status().send()
    } catch (error) {
      console.error(error);
      return; // replace with res.status().send()
    }
  };
}
const service = new Freshsales();
export default service;
